@import "../global.scss";

.Menu {
    width: 280px;
    height: 40vh;
    background-color: transparent;
    position: fixed;
    top: 20px;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    &.active {
        right: 0;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 20px;
        font-weight: 300;
        color: white;
        width: 60%;
    }

    li {
        margin-bottom: 15px;

        a {
            font-size: inherit;
            color: inherit;
            text-decoration: none;
        }

        &:hover {
            font-weight: 500;
        }
    }
}

@media only screen and (max-width: 768px) {
    .Menu {
        width: 240px;
        height: 40vh;
        right: -260px;

        &.active {
            right: 0;
        }

        ul {
            font-size: 18px;
            width: 70%;
        }

        li {
            margin-bottom: 12px;
        }
    }
}
