@import "../global.scss";

.Contact {
  background: linear-gradient(135deg, #11122a 0%, #0a0e1a 100%);
  color: #e0e0ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
   box-sizing: border-box;
  padding: 4rem 2rem;
  min-height: 60vh;
  width: 100%;

  .contact-title {
    font-size: 2.5rem;
    margin-bottom: 3rem;
    color: #99ccff;
    font-weight: 300;
    text-align: center;
  }

  .contact-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    max-width: 800px;
    width: 100%;
  }

  .contact-link {
    display: flex;
    overflow-y: hidden;
    
    display: flex;
        flex-direction: column;
        align-items: center; 
        justify-content: center; 
        text-align: center;

     box-sizing: border-box;
    
    background: rgba(153, 204, 255, 0.1);
    border: 1px solid rgba(153, 204, 255, 0.2);
    border-radius: 12px;
    padding: 1.5rem;
    width: 150px;
    height: 150px;
    text-decoration: none;
    color: #e0e0ff;
    transition: all 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);

    &:hover {
      transform: translateY(-5px);
      background: rgba(153, 204, 255, 0.2);
      box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
    }

    .icon-img {
      width: 60px;
      height: 60px;
      object-fit: cover;
      border-radius: 50%;
      margin-bottom: 1rem;
      border: 2px solid #99ccff;
    }

    span {
      font-size: 1.2rem;
      font-weight: 300;
    }
  }

  @include mobile {
    padding: 3rem 1rem;
    min-height: 50vh;

    .contact-title {
      font-size: 2rem;
      margin-bottom: 2rem;
    }

    .contact-link {
      width: 120px;
      height: 120px;
      padding: 1rem;

      .icon-img {
        width: 50px;
        height: 50px;
      }
    }
  }
}

@media (max-width: 768px) {
  .Contact {
    padding: 1.5rem 1rem;
    width: 100%;

    .contact-title {
      font-size: 28px;
      text-align: center;
      margin-bottom: 20px;
    }

    .contact-container {
      flex-direction: column;
      align-items: center;
      text-align: center;
      display: flex;
        flex-direction: column;
        align-items: center; 
        justify-content: center; 
        text-align: center;
      gap: 20px;
      padding: 0 1rem;

      .contact-link {
        width: 100%;
        max-width: 250px;
        justify-content: center;
        gap: 10px;
        display: flex;
        flex-direction: column;
        align-items: center; 
        justify-content: center; 
        text-align: center;

        img.icon-img {
          width: 24px;
          height: 24px;
        }

        span {
          font-size: 16px;
        }
      }
    }
  }
}
