@import "../global.scss";

.Topbar{
    width: 100%;
    height: 65px;
    background-color: transparent;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

    }
    


    .right{
        .hamburger{
            width: 32px;
            height: 25px;
            display: flex;
            position: center;
            flex-direction: column;
            justify-content: space-between;
            cursor: pointer;

            span{
                width: 100%;
                height: 3px;
                background-color: white;
                transform-origin: left;
                transition: all 2s ease;
            }
        }
    }
    &.active{
        color: white;

        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);

                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}

@media (max-width: 768px) {
  .Topbar {
    .wrapper {
      flex-direction: row;
      justify-content: space-between;
      padding: 10px 15px;

      .hamburger {
        display: flex;
      }
    }
  }
}
