@import "../global.scss";

.Intro {
  background: radial-gradient(ellipse at top left, #11122a 0%, #000d1a 100%);
  color: #e0e0ff;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100vh;
  overflow: hidden;
 
 
 
  .left {
    flex: 0.5;
    position: relative;
    height: 100%;
    overflow: hidden; // Ensure only one slide is visible
    border-radius: 20px; // Add this line for rounded corners
    margin: 20px; // Add some margin so the rounded corners are visible
    
    .slider-container {
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;
          border-radius: 18px;
    }
   
    .slider {
      display: flex;
      width: 300%; // 3 slides Ã— 100% each
      height: 100%;
      transition: transform 0.5s ease-in-out;
      position: relative;
     
      .slide {
        width: 33.333%; // Each slide takes exactly 1/3 of the slider
        height: 80%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 2rem;
       
        .slide-title {
          font-size: 2rem;
          color: #99ccff;
          font-weight: 300;
          margin-bottom: 1rem;
          text-align: center;
        }
       
        .image-container {
          width: 80%;
          height: 70%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          border-radius: 12px;
         
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
           border-radius: 10px;
          }
        }
      }
    }
   
    .arrow {
      width: 40px;
      height: auto;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 5;
      cursor: pointer;
      opacity: 0.7;
      transition: opacity 0.3s;
     
      &:hover {
        opacity: 1;
      }
     
      &.left {
        left: 20px;
      }
     
      &.right {
        right: 20px;
      }
    }
  }
 
  .right {
    flex: 0.5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem;
   
    .aboutTitle {
      font-size: 50px;
      margin-bottom: 1rem;
      color: #99ccff;
      font-weight: 200;
    }
   
    .aboutMeHeader {
      font-size: 1.1rem;
      line-height: 1.6;
      color: whitesmoke;
      max-width: 600px;
      margin: 0 auto;
      padding: 0 1rem;
      
      p {
        margin-bottom: 1.2rem;
      }
    }
  }
}

// Update the mobile media query in Intro.scss
@media (max-width: 768px) {
  .Intro {
    flex-direction: column;
    height: auto;
    padding: 1rem;

    .left, .right {
      flex: none;
      width: 100%;
      margin: 0;
    }

    .left {
      height: 250px; // Fixed height for mobile
      margin-bottom: 1rem;
      margin: 0 auto 1rem auto;
      width: 90%;
      
      .slider-container {
        height: 100%;
      }
      
      .slider {
        .slide {
          padding: 0.5rem;
          
          .slide-title {
            font-size: 1.2rem;
            margin-bottom: 0.5rem;
          }
          
          .image-container {
            height: 150px;
          }
        }
      }
    }

    .right {
      padding: 0 1rem;
      
      .aboutTitle {
        font-size: 1.8rem;
        text-align: center;
        margin-bottom: 0.5rem;
      }
      
      .aboutMeHeader {
        font-size: 0.9rem;
        padding: 0 1rem;
        
        p {
          margin-bottom: 0.8rem;
          text-align: left; // Better for readability
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .Intro {
    padding: 1rem;

    .left {
      .slider-container {
        height: 250px;
      }

      .slider .slide .image-container {
        height: 180px;
      }

      .arrow {
        width: 24px;
      }
    }

    .right {
      .aboutTitle {
        font-size: 26px;
      }

      .aboutMeHeader {
        font-size: 0.95rem;
      }
    }
  }
}
