.Portfolio{
  background: linear-gradient(135deg, #11122a 0%, #0a0e1a 100%);   
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 40px 20px;
  width: 100%;

  h1 {
    font-size: 80px;
    color: white;
    margin-bottom: 40px;
    font-weight: 100;
    overflow-y: hidden;
  }

  .container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 30px;
    position: relative;
  }

  .card {
    width: 250px;
    min-height: 350px;
    border-radius: 10px;
    background-color: #1e1e2f;
    box-shadow: 0px 0px 15px -8px black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    color: white;
    font-weight: 300;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    cursor: pointer;
    position: relative;
    overflow: hidden;


    &.expanded {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(1.1);
      width: 80%;
      max-width: 600px;
      height: auto;
      min-height: 500px;
      z-index: 100;
      background-color: #2a2a3a;
      box-shadow: 0px 0px 30px -5px rgba(0, 0, 0, 0.8);
      
     .center {
        padding: 20px;
        overflow-y: hidden;
        max-height: 300px;
        display: flex;
        flex-direction: column;
        align-items: center; 
        justify-content: center; 
        text-align: center;
      }

      p {
      text-align: center;
    }
    }

    &.featured {
      width: 300px;
      min-height: 400px;
      margin: 0;
    }

    &:hover:not(.expanded) {
      transform: scale(1.05);
      box-shadow: 0px 0px 20px -5px black;
    }

    

    h2 {
      color: black;
      font-weight: 100;
      text-align: center;
      margin: 10px 0;
    }

    h3 {
      color: white;
      font-weight: 100;
      margin-bottom: 5px;
      text-align: center;
    }

    .top {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;

  h3 {
    color: white;
    font-weight: 100;
    text-align: center;
    margin: 0;
  }
    }

    .center {
      padding: 15px;
      border-radius: 10px;
      background-color: rgba(255, 255, 255, 0.9);
      color: #333;
      flex-grow: 1;
      margin: 15px 0;
      transition: all 0.3s ease;
      display: flex;
      flex-direction: column;
      align-items: center; 
      justify-content: center; 
      text-align: center;

      .desc {
        display: flex;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .body {
        display: block;
      }
    }

    .bottom {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
    }
  }

  /* Backdrop for expanded card */
  &::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    z-index: 99;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;
  }

  /* Show backdrop when card is expanded */
  &.has-expanded::before {
    opacity: 1;
    pointer-events: auto;
  }
}

@media (max-width: 480px) {
  .Portfolio {
    padding: 1rem;
    
    h1 {
      text-align: center;
    font-size: 2rem;
    padding: 0 1rem;
    }
    
    .container {
      gap: 15px;
    }
    
    .card {
      width: 90%;
      max-width: 100%;
    box-sizing: border-box;
    padding: 1rem;
      
      &.expanded {
        width: 90vw;
        max-height: 80vh;
        padding: 15px;
        
        .center {
          max-height: 60vh;
          overflow-y: auto; // Allow scrolling for long content
          padding: 1rem;
      font-size: 0.9rem;
        }
      }
      
      h2, h3 {
        font-size: 1rem;
      }
      
      .center {
        padding: 10px;
        font-size: 0.9rem;
      }
    }
  }
}