@keyframes gradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.app {
  min-height: 100vh;
  height: auto;
  overflow-x: hidden; // Prevent horizontal scroll

  .sections {
    width: 100%;
    height: calc(100vh - 70px);
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;

    > * {
      width: 100%;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        display: none;
      }
      padding: 1rem;
      box-sizing: border-box;
    }
  }
}

.startPage {
  width: 100%;
  height: 100vh;
  position: relative;
  background: linear-gradient(-45deg, #0a0a2a, #001133, #002244, #000d1a);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  overflow: hidden; // Prevent content from causing scroll

  .title1 {
    position: absolute;
    top: 15%; 
    left: 35%;
    transform: translateX(-50%);
    color: whitesmoke;
    font-size: 250px;
    font-weight: 50;
    white-space: nowrap;
  }

  .title2 {
    position: absolute;
    top: 40%; 
    left:65%;
    transform: translateX(-50%);
    color: whitesmoke;
    font-size: 250px;
    font-weight: 50;
    white-space: nowrap;
  }

  h2 {
    position: absolute;
    top: 75%; 
    left: 50%;    
    transform: translateX(-50%);
    color: whitesmoke;
    font-weight: 300;
  }
}

// Responsive adjustments
@media (max-width: 1600px) {
  .startPage {
    .title1, .title2 {
      font-size: 12vw;
    }
  }
}

@media (max-width: 1200px) {
  .startPage {
    .title1 {
      left: 25%;
    }
    .title2 {
      left: 65%;
    }
  }
}

@media (max-width: 768px) {
  .startPage {
    .title1, .title2 {
      font-size: 20vw;            // Responsive scaling, similar to desktop
      white-space: nowrap;        // Keep titles on one line
    }

    .title1 {
      top: 15%;
      left: 35%;
      transform: translateX(-50%);
    }

    .title2 {
      top: 40%;
      left: 62%;
      transform: translateX(-50%);
    }

    h2 {
      top: 60%;
      font-size: 1rem;
      width: 100%;
      text-align: center;
    }
  }
}

